import React, { Component } from 'react';
import cards from "./cards.json";


function Cards(props) {
  return (
      <div className="img-container">
        <img alt={props.name} src={props.image} />
      </div>
  );
}



class App extends Component {
  state = {
    cards,
    score: 0,
    highScore: localStorage.highScore,
    maxScore: 12,
    showAlert: false
  };

  handleShowAlert() {
    this.setState({
      showAlert: true
    });
  }

  gameOver = () => {
    if (this.state.score > this.state.highScore) {
      this.setState({highScore: this.state.score});
      localStorage.highScore = this.state.score
    }
    this.state.cards.forEach(card => {
      card.click = false;
    });
 
    //alert("Game Over");
    this.handleShowAlert();
    this.setState({score: 0});

        
    setTimeout(() => {
      this.setState({
        showAlert: false
      });
      window.location.reload();
    }, 1500);

    return true;
  }
  
  shuffle = function shuffle(cards) {
    var i,
        j,
        temp;
    for (i = cards.length - 1; i > 0; i--) {
        j = Math.floor(Math.random() * (i + 1));
        temp = cards[i];
        cards[i] = cards[j];
        cards[j] = temp;
    }
    return cards;    
};

  clicked = id => {

    const idx = cards.findIndex( f => f.id === id );
    
    if( cards[idx].click === true){
      this.gameOver();
    }else{
      cards[idx].click= true;
      this.setState({score: this.state.score+1});
      
      const shuffledDeck = this.shuffle(cards);
      this.setState({cards: shuffledDeck});
    }

  }


  
  render() {
    return (
      <div>
        <nav className="navbar">
          <div className="title">Clicky-Game</div>
          <a className= "resetGame" href="/">Reset</a>
          <div className="scoreboard">Score: {this.state.score} | High Score: {this.state.highScore}</div>
        </nav>
        <div className="alertbox">
        <div className={`alert alert-success ${this.state.showAlert ? 'alert-shown' : 'alert-hidden'}`}>
          <strong>GAME OVER!</strong>
        </div>
        </div>
        <div className="wrapper">
        {this.state.cards.map(card => (
         <button key={card.id} onClick={()=>this.clicked(card.id)}>
         <Cards
            id={card.id}
            key={card.id}
            name={card.name}
            image={card.image}
          />
          </button>
        ))}
        </div>
      </div>
    );
  }
}

export default App;
